body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
    background: powderblue;
    min-height: 100vh;
    overflow-y: scroll;
}


ol, ul {
    padding-left: 30px;
}

.board-row {
    display: flex;
}

.board-row:after {
    clear: both;
    content: "";
}

@keyframes glowing {
    0% {
        background-color: #2ba805;
        box-shadow: 0 0 10px #2ba805;
    }
    50% {
        background-color: #49e819;
        box-shadow: 0 0 40px #49e819;
    }
    100% {
        background-color: #2ba805;
        box-shadow: 0 0 10px #2ba805;
    }
}

.status {
    margin-bottom: 10px;
}

.winner {
    background: red !important;
    box-shadow: 0 0px 14px 4px red;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 34px;
}
.ui.table tbody tr td.selectable {
    padding: 10px;
}
.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

.gameContainer {
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    top: 0;
}

.game-info {
    margin-left: 40px;
}

.game-board {
    display: flex;
    margin: auto;
    width: fit-content;
}

.game-history {
    padding: 20px 0px;
}

.root{
    overflow-y: scroll;
}

.squaresHolder {
    animation: glowing 1300ms infinite;
}

.ui {
    width: 100%;
}

@media only screen and (max-width: 767px){
    .ui.container {
        width: max-content !important;
    }
}

.row{
    margin: auto;
}